  display: grid;
}

.board .game-board-square.x:before, .board .game-board-square.x:after, .board.x .game-board-square:not(.x):not(.o):hover:before, .board.x .game-board-square:not(.x):not(.o):hover:after {
  content: "";
  width: 1rem;
  height: 5rem;
  position: absolute;
