  transform: rotate(-45deg);
}

.board.x .game-board-square:not(.x):not(.o):hover:before, .board.x .game-board-square:not(.x):not(.o):hover:after {
  box-shadow: none;
}

.board.o .game-board-square:not(.x):not(.o):hover:before, .board .game-board-square.o:before {
